html, * {
  margin: 0;
  padding: 0;
}
h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}
#app {
  font-family: Helvetica Neue, Helvetica, Arial, 'Hiragino Kaku Gothic ProN', Meiryo, sans-serif;
  -webkit-text-size-adjust: 100%;
}
.wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
}
.tools {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #eee;
  width: 400px;
  min-height: 100vh;
  padding: 20px;
  font-size: 11px;
  line-height: 1.1;
  font-family: 'Source Code Pro', monospace;
  font-weight: 400;
  letter-spacing: .02em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  -webkit-font-feature-settings: "palt";
          font-feature-settings: "palt";
}
.tools.js-tools-active {
    display: none;
}
.tools__item {
  margin-bottom: 15px;
}
.tools__item:last-child {
    margin-bottom: 0;
}
.tools__block {
  margin-top: 7px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.tools__block input[type="text"],
  .tools__block input[type="number"],
  .tools__block input[type="color"],
  .tools__block input[type="range"],
  .tools__block label {
    margin-right: 10px;
    letter-spacing: -0.025em;
}
.tools__block input[type="radio"] {
    margin-right: 4px;
}
.tools__block textarea {
    font-family: Helvetica Neue, Helvetica, Arial, 'Hiragino Kaku Gothic ProN', Meiryo, sans-serif;
    font-weight: normal;
    width: 100%;
    max-width: 100%;
    height: 120px;
    line-height: 1.4;
    padding: 8px;
    letter-spacing: .05em;
}
.tools__radio {
  margin-bottom: 5px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.tools__radio:nth-last-child(1), .tools__radio:nth-last-child(2) {
    margin-bottom: 0;
}
.tools__label {
  font-size: 10px;
  font-weight: 700;
  background: #444;
  color: #fff;
  padding: 3px 6px;
  border-radius: 2px;
}
.container {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: calc(100% - 400px);
  min-height: 100vh;
}
.container.js-tools-active {
    width: 100%;
}
.css {
  letter-spacing: .02em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  -webkit-font-feature-settings: "palt";
          font-feature-settings: "palt";
}
.css pre {
    font-family: 'Source Code Pro', monospace;
    font-weight: 400;
    background: #262a34;
    padding: 20px;
    color: #b8bdc7;
    font-size: 12px;
    line-height: 1.5;
}
.container__block {
  white-space: pre-line;
}
.toggle {
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  font-family: 'Source Code Pro', monospace;
  font-weight: 400;
  letter-spacing: .02em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  -webkit-font-feature-settings: "palt";
          font-feature-settings: "palt";
}
.toggle__button {
  margin-right: 10px;
  font-size: 11px;
  line-height: 1;
}
.toggle__button:last-child {
    margin-right: 0;
}
.toggle__anchor {
  display: block;
  padding: 4px 8px;
  background: #fff;
  border: #222 1px solid;
  color: #222;
  text-decoration: none;
  border-radius: 2px;
  letter-spacing: -0.01em;
}
.toggle__anchor:hover {
    background: #eee;
}

